import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import uniSteps from '@/components/uni-steps/uni-steps.vue';
import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue';
import json from '@/json';

export default {
	components: {
		uniNavBar,
		uniSteps,
		uniList,
		uniListItem,
		uniPopup
	},
	data() {
		return {
			id:"",
			showtip1: 'center',
			detailData: {},
			imgList: [],
			active: 1,
type1:[],
category:[],
			userFeedbackHidden: true, // 默认隐藏
			feedbackContent: '', // 用户反馈内容
			latitude: 30.663429,
			longitude: 104.072422,
			covers: [
				{
					latitude: 30.663429,
					longitude: 104.072422,
					iconPath: '../../static/location.png',
					width: 50,
					height: 50
				}
			],
			isfull:false
		};
	},
	onLoad(options) {
		var detailData = JSON.parse(options.data);
		this.id=detailData.id;
		console.log(this.id)
		// this.detailData = json.detail.data;
		//this.loadNewsList();
		//获取上报事件
		this.getType();
		//获取上报类型
		this.getType1()
	},
	onReady(){this.loadNewsList()},
	methods: {
			
		clk(index1){
			if(!this.isfull){
				console.log(index1)
				this.videoContext = uni.createVideoContext("'vi'+index1");
				this.videoContext.requestFullScreen();
				this.videoContext.play();
				this.isfull=!this.isfull
			}
			
		},
		getType() {
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-sjlxxz.htm', { params: {} })
				.then(res => {
					console.log('事件类型');
					console.log(res);
					if (res.data.msgState == 1) {
						this.type1= res.data.list;
						
					} else {
						uni.showLoading({
							title: '获取事件类型失败'
						});
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		getType1() {
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-sjlbxz.htm', { params: {} })
				.then(res => {
					console.log('事件类别');
					console.log(res);
					if (res.data.msgState == 1) {
							this.category=res.data.list
					} else {
						uni.showLoading({
							title: '获取事件类别失败'
						});
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		//处理函数
		typeChange() {
			
			console.log( this.type1)
			for (let i = 0; i < this.type1.length; i++) {
				if(this.type1[i].id==this.detailData.type){
					this.detailData.type=this.type1[i].name
				}
			}
			return '无';
		},
		//处理函数
		typeChange1(val) {
			console.log(val)
			console.log( this.category)
			for (let i = 0; i < this.category.length; i++) {
				if(this.category[i].id==this.detailData.category){
					this.detailData.category=this.category[i].name
				}
			}
			return '无';
		},
		//获取推荐列表
		async loadNewsList() {
			uni.showLoading({
				title: '加载中...'
			});
			// this.detailData = json.detail.data;
			// console.log(this.detailData);
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-sjzx-sjxq.htm', {
					params: {
						id: this.id
					}
				})
				.then(res => {
					uni.hideLoading();
					console.log(res);
					if (res.data.msgState == 1) {
						var list = res.data.list;
						console.log('list');
						console.log(list);
						this.detailData=list[0];
						this.covers[0].latitude= list[0].latitude;
						this.covers[0].longitude= list[0].longitude;
						//处理进度
						this.processingProcess()
						//处理图片
					//	this.imgsrc()
						//处理类型
					   this.typeChange1()
					   this.typeChange()
					} else {
						uni.showToast({
							title:"请求失败"
						})
					}
				})
				.catch(err => {
					console.log(err);
					uni.hideLoading();
				});
		},
		imgsrc(){
			if(this.detailData.imgsrc.length>0){
				var str=this.detailData.imgsrc.split(',');
				console.log(str)
				this.detailData.imgList=[...str]
				
			}
			
		},
		processingProcess(){
			var str=this.detailData.jd.split(',');
			console.log(str)
			var processingProcess=[]
			for (var i = 0; i < str.length; i++) {
				var os=str[i].split('  ');
				console.log(os)
				var obj={}
				obj.title=os[0];
				obj.desc=os[1];
				processingProcess.push(obj)
			}
			console.log(processingProcess)
			this.detailData.processingProcess=processingProcess
			
		},
		statusChange(index) {
			switch (index) {
				case 0:
					return '待处理';
					break;
				case 1:
					return '待办';
					break;
				case 2:
					return '待办结';
					break;

				case 3:
					return '已办结';
					break;
					case 4:
						return '无效';
						break;
				default:
					return '待处理';
			}
		},
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		//打电话
		callPhone(phone) {
			console.log(phone);
			uni.makePhoneCall({
				phoneNumber: phone //仅为示例
			});
		},
		scroll(e) {
			console.log(e.detail.scrollTop);
		},
		//图片预览
		previewImage(e) {
			var current = e.target.dataset.src;
			console.log('current');
			console.log(current);
			uni.previewImage({
				current: current,
				urls: this.detailData.imgsrc
			});
		},
		submitMsg() {
			if(this.detailData.status==3||this.detailData.status==4){
				return
			}
			console.log('確認完成');
			this.$nextTick(() => {
				this.$refs.showtip.open();
			});
		},
		alertMsg() {
			console.log('我要反馈');
			this.$nextTick(() => {
				this.$refs.showtip1.open();
			});
		},

		submitFeedbackContent() {
			
			var obj={feedback_content:this.feedbackContent,id:this.id}
			//发起请求
			this.$refs.showtip1.close();
			uni.showLoading({
				title: 'loading'
			});
			this.$http.post('/interface/rest/http/xlwb/xlgc-wb-xcx-sjzx-wyfk.htm',obj).then(res => {
				uni.hideLoading();
				console.log(res.data);
				if(res.data.msgState==1){
					
					this.detailData.feedback_content = this.feedbackContent;
					
					
				}
				uni.showToast({
				    title: res.data.msg,
				    duration: 2000
				});
			}).catch(err => {
				console.log(err);
				uni.hideLoading();
			})
			
		},
		submitOver(){
				this.$refs.showtip.close();
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-sjzx-clwc.htm', {
					params: {
							id: this.id
					}
				})
				.then(res => {
					console.log(res);
					if (res.data.msgState == 1) {
						uni.showToast({
						    title: res.data.msg,
						    duration: 2000
						});
						setTimeout(() => {
							this.back()
						}, 1000);
						 
					} else {
						uni.showLoading({
							title: '请求失败'
						});
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		cancel(type) {
			this.$refs.showtip.close();
			this.$refs.showtip1.close();
		}
	}
};
